<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Alta de Clientes
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarCliente()">
          <v-text-field
            v-model="customer.name"
            label="Nombre Completo"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="customer.document"
            label="DNI"
            :counter="8"
            :rules="dniRules"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="customer.cellphone"
            label="Celular"
            :counter="9"
            :rules="cellRules"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="customer.email"
            label="Email"
            :rules="emailRules"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="customer.address"
            label="Dirección"
            outlined
            prepend-icon="mdi-lock"
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas/clientes"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlCliente = 'https://sistema-adv.herokuapp.com/api/customer/'

  export default {
    name: 'CrearCliente',
    data () {
      return {
        customer: {
          name: '',
          document: '',
          cellphone: '',
          email: '',
          address: '',
        },
        dniRules: [
          (v) => !!v || 'DNI es requerido',
          (v) => v.length <= 8 || 'DNI no debe ser mayor a 8 dígitos',
        ],
        cellRules: [
          (v) => !!v || 'Celular es requerido',
          (v) => v.length <= 9 || 'Celular no debe ser mayor a 9 dígitos',
        ],
        emailRules: [
          (v) => !!v || 'E-mail es requerido',
          (v) => /.+@.+/.test(v) || 'Ingresa un email válido',
        ],
      }
    },
    mounted () {},
    methods: {
      guardarCliente () {
        const router = this.$router
        const params = this.customer
        axios.post(urlCliente, params).then((res) => {
          // console.log(res);
          router.push('/ventas/clientes')
          this.limpiarCampos()
        })
      },
      limpiarCampos () {
        this.customer = ''
      },
    },
  }
</script>
